<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
			消费统计
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonetap">
					<view class="stapleft">
						<view class="staplefts">{{list.currTotal}}</view>
						<view class="stapleftx">今日消费（元）</view>
					</view>
					<view class="stapleft">
						<view class="staplefts">{{list.monthTotal}}</view>
						<view class="stapleftx">当月消费（元）</view>
					</view>
				</view>
			</view>
			<view class="sone1">
				<input v-model="name" placeholder='输入小区名搜索'/>
				<view class="sonefoot" @click="sbtn">
					<image src="../../static/tabBarimg/search.png"></image>
				</view>
			</view>
			<view class="stwo">
				<view class="stwotap" @click="detailbtn(item.projectId)" v-for="(item, index) in list.list" :key="index">
					<view class="stwoleft">
						<view class="stwolefts">{{item.projectName}}</view>
						<view class="stwoleftx">本月消费¥{{item.monthBalance}}，今日消费¥{{item.currBalance}}</view>
					</view>
					<image src="../../static/tabBarimg/yright.png"></image>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	
	export default {
		components: {},
		mounted() {
			
		},

		data() {
			return {
				list:{},//列表数据
				name:''
			}
		},
		onLoad() {
			this.show()
		},
		methods: {
			//消费统计
			show(){
				var data={
					projectName:this.name
				}
				uni.showLoading({
					title: '加载中...'
				})
				this.$base.request1('manager/statistic/consume', 'GET',data)
					.then(res => {
						uni.hideLoading()
						this.list=res.data.data
					})
					.catch(err => {
				
					})
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			//跳转详情
			detailbtn(projectId){
				uni.navigateTo({
				       url: '/pages/consumption_statistics_detail/consumption_statistics_detail?projectId='+projectId,
				});
			},
			//搜索
			sbtn(){
				this.show()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;
