			line-height: 62upx;
			color: #ffffff;
			position: relative;
			image{
				width:22upx;
				height: 39upx;
				position: absolute;
				left: 31upx;
				top: 11upx;
			}
		}
		.show{
			 width: 100%;
			 box-sizing: border-box;
			.sone{
				width: 100%;
				height: 253upx;
				background-color: #4B98ED;
				position: relative;
				.sonetap{
					width: 686upx;
					height: 205upx;
					border-radius: 16upx 16upx 0upx 0upx;
					box-shadow: 0upx 0upx 9upx 0upx rgba(66,66,66,0.17); 
					background-color: #FFFFFF;
					position: absolute;
					bottom: 0upx;
