					margin-left: -343upx;
					display: flex;
					justify-content: space-between;
					padding: 55upx 0upx 29upx 0upx;
					box-sizing: border-box;
					.stapleft{
						width: 50%;
						border-right: 2upx solid #999999;
						.staplefts{
							font-size: 48upx;
							font-family: Arial Bold, Arial Bold-Bold;
							font-weight: 700;
							text-align: center;
							color: #4b98ed;
						}
						.stapleftx{
							font-size: 28upx;
							font-family: PingFang SC Medium, PingFang SC Medium-Medium;
							font-weight: 500;
							text-align: center;
							color: #333333;
						}
					}
					
				}
			}
			.sone1{
				width: 650upx;
				box-sizing: border-box;
				display: flex;
				align-items: center;
				margin: 32upx auto;
				input{
					width: 562upx;
