			.stwo{
				width: 100%;
				box-sizing: border-box;
				padding: 25upx 32upx;
				.stwotap{
					width: 686upx;
					border: 1upx solid #eaeaea;
					display: flex;
					justify-content: space-between;
					box-sizing: border-box;
					padding: 32upx 23upx;
					align-items: center;
					.stwoleft{
						.stwolefts{
							font-size: 28upx;
							font-family: PingFang SC Bold, PingFang SC Bold-Bold;
							font-weight: 700;
							color: #333333;
							margin-bottom: 10upx;
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
							width: 500upx;
						}
						.stwoleftx{
							font-size: 24upx;
							font-family: PingFang SC Medium, PingFang SC Medium-Medium;
							font-weight: 500;
							color: #666666;
